<script>
//头部
import CommonHeader from '../../public/header.vue';
//tab导航
import Set from '../../public/set.vue';
import publicFn from '../../../untils/Public.js';
import { mapActions, mapGetters, mapState } from "vuex"
export default {
  data: function() {
    return {
      //病例
      recordList: [],

      OrderChart: {
        pbStartTime: { ymd: "", hms: "" },
        pbEndTime: { ymd: "", hms: "" }
      },

      clicnChart: {},

      orderId: 0,
      recordObject: {},
      arrowRection: [],
      tookingList: null,
      expertURL: null,
      docterURL: null,
      type: 1,
      user: null,
      uclcinDate: 1,
      isBlowUp: false,//放大
      blowUpImgUrl: '',
      proposalShow:false
    }
  },
  methods: {
    ...mapActions([
      'postOderChart'
    ]),
    talkDetails: function() {

    },
    patientDetails: function() {

    },
    proposal: function() {

    },
    arrow(index) {
      this.arrowRection[index] = !this.arrowRection[index]
      for (var i = 0, len = this.arrowRection.length; i < len; i++) {
        if (i != index && this.arrowRection[i] == false) {
          this.arrowRection[i] = true
        }
      }
      this.arrowRection = this.arrowRection.concat()
    },
    //    返回协诊订单
    backOrder: function() {
      if (this.$route.query.type) {
        this.$router.push({ name: 'AssistOrderIng', query: this.$route.query.back })
      } else {
        this.$router.push({ name: 'AssistOrders' })
      }
    },
    //    打印
    printFn: function() {
      this.$router.push({ name: 'Print' })
    },
    // 主要信息
    lists: function() {
      var id = this.$route.query.id;
      var type = this.$route.query.type;
      if (type) {
        this.type = type
      }
      this.postOderChart(id)
    },
    //订单信息
    oderChartData(res) {
      this.expertURL = res.expertImageUrl;
      this.docterURL = res.doctorImageUrl;
      var ageTime = publicFn.publicLo.dataChange(res.birthday);
      var newTime = publicFn.publicLo.dataChange();
      var age = parseInt(newTime.y) - parseInt(ageTime.y);
      var endTime = { times: "" };
      var startTime = { times: "" };
      var pbStartTime = { ymd: "", hms: "" };
      var pbEndTime = { ymd: "", hms: "" };
      if (res.endTime) {
        endTime = publicFn.publicLo.timeCheng(res.endTime);
      }
      if (res.startTime) {
        startTime = publicFn.publicLo.timeCheng(res.startTime)
      }
      if (res.pbStartTime) {
        pbStartTime = publicFn.publicLo.timeCheng(res.pbStartTime);
      }
      if (res.pbEndTime) {
        pbEndTime = publicFn.publicLo.timeCheng(res.pbEndTime)
      }
      var date = {
        expertName: res.expertName,
        doctorTitle: res.doctorTitle,
        doctorName: res.doctorName,
        createdWhen:res.createdWhen,
        doctorId: res.doctorId,
        price: res.price,
        type: res.type,
        recordId: res.recordId,
        hospitalId: res.hospitalId,
        patientName: res.patientName,
        description: res.res,
        expertTitle: res.expertTitle,
        startTime: startTime,
        id: res.id,
        scheduleId: res.scheduleId,
        skuId: res.skuId,
        orderNo: res.orderNo,
        recordType: res.recordType,
        sex: res.sex,
        paidIn: res.paidIn,
        usersId: res.usersId,
        endTime: endTime,
        age: age,
        status: res.status,
        pbStartTime: pbStartTime,
        pbEndTime: pbEndTime
      };

      // date.type="图文问诊"
      if (date.type == 0) {
        date.type = "图文问诊"
      }
      if (date.type == 1) {
        date.type = "视频面诊"
      }
      if (date.sex == 1) {
        date.sex = "男";
      }
      if (date.sex == 0) {
        date.sex = "女";
      }
      //Tab title 订单状态 0:待付款 1:已预约 2:协诊中 3:已完成 4:未出诊 5:失效 6:已确认 7:异常状态
      switch (date.status) {
        case 0:
          date.statusText = " 待付款";
          break;
        case 1:
          date.statusText = "已预约 ";
          break;
        case 2:
          date.statusText = " 协诊中";
          break;
        case 3:
          date.statusText = " 已完成";
          break;
        case 4:
          date.statusText = "未出诊 ";
          break;
        case 5:
          date.statusText = " 失效";
          break;
        case 6:
          date.statusText = " 已确认";
          break;
        case 7:
          date.statusText = " 异常状态";
          break
      }
      if (date.recordType == 1) { date.recordType = '高血压' }
      if (date.recordType == 2) { date.recordType = '心电心理' }
      if (date.recordType == 3) { date.recordType = '冠脉血管' }
      if (date.recordType == 4) { date.recordType = '心力衰竭' }
      date.endTime = date.endTime.times;
      date.startTime = date.startTime.times;
      this.OrderChart = date;
      this.uclcin(date.id);
      this.medicalRecord(res.recordId);
      this.tooking(date.id);
      this.orderId = res.recordId;

      // 把要打印的数据存在本地
      localStorage.setItem('OrderChart', JSON.stringify(this.OrderChart));
    },
    // 诊疗建议
    uclcin: function(id) {
      var url = '/api/assist-order/uClinics/' + id;
      var token = localStorage.getItem('access_token');
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + token
        }
      }).then(function(res) {
        this.proposalShow=true
        var res = res.body;
        this.uclcinDate = 1;
        var createdWhen = publicFn.publicLo.timeCheng(res.createdWhen);
        var updatedWhen = publicFn.publicLo.timeCheng(res.updatedWhen);
        var date = {
          id: res.id,
          clinicsNo: res.clinicsNo,
          orderId: res.orderId,
          usersId: res.usersId,
          type: res.type,
          doctorId: res.doctorId,
          expertId: res.expertId,
          diagnosisMemo: res.diagnosisMemo,
          treatmentMemo: res.treatmentMemo,
          deleteFlag: res.deleteFlag,
          createdBy: res.createdBy,
          createdWhen: createdWhen.ymd,
          updatedBy: res.updatedBy,
          updatedWhen: updatedWhen.ymd
        };
        if (date.type == 1) { date.type = '高血压' }
        if (date.type == 2) { date.type = '心电生理' }
        if (date.type == 3) { date.type = '冠脉血管' }
        if (date.type == 4) { date.type = '心力衰竭' }
        this.clicnChart = date;
        localStorage.setItem('clicnChart', JSON.stringify(this.clicnChart));
      }).catch(function() {
        this.proposalShow=false
        this.uclcinDate = null
      });
    },
    // 患者病历
    medicalRecord: function(id,time) {
      this.Authorization = localStorage.getItem('access_token');
      time=publicFn.publicLo.timeCheng(time);
      var url = '/api/patient/records/list/'+id;
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        }
      }).then(function(res) {
        var pu=[];
        var first={};
        for (let i in res.body) {
          res.body[i].createdWhen = publicFn.publicLo.timeCheng(res.body[i].createdWhen);
          res.body[i].createdWhen = " " + res.body[i].createdWhen.ymd;
          if( res.body[i].id==id){
            first= res.body[i]
          }else {
            pu.push(res.body[i])
          }
        };
        pu.unshift(first);
        this.recordList = pu;
//        for (var i = 0; i < res.body.length; i++) {
//          if (i == 0) {
//            this.arrowRection.push(false)
//          } else {
//            this.arrowRection.push(true)
//          }
//
//        }
      })
    },
    //对话详情
    tooking: function(id) {
      var token = localStorage.getItem('access_token');
      var url = '/api/assist-order/uDialogueRecord/' + this.$route.query.id;
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + token
        }
      }).then(function(res) {
        this.tookingList = [];
        res = res.body;
        for (let i in res) {
          var time = publicFn.publicLo.timeCheng(res[i].createdWhen);
          time = time.times;
          if (res[i].dialogueContent.data) {
            res[i].dialogueContent.data = "";
          }
          var par = res[i].dialogueContent;
          if (typeof (res[i].dialogueContent) == "string") {
            par = JSON.parse(res[i].dialogueContent);
          }
          var date = {
            "id": res[i].id,
            "userId": res[i].userId,
            "time": time,
            "dialogueContent": par,
            "orderId": res[i].orderId,
            "type": res[i].type,
            "createdBy": res[i].createdBy
          }
          this.tookingList.push(date);
        }
      })
    },
    blowUp(url) {
      this.isBlowUp = true;
      this.blowUpImgUrl = url.path;
    },
    isBlowUpShow() {
      this.isBlowUp = false;
    }
  },
  mounted: function() {
    this.lists();
  },
  computed: {
    //订单详情
    oder: function() {
      var data = this.oderChart;
      if (data) {
        this.oderChartData(data)
      }
      return 1;
    },
    ...mapState([
      'oderChart'
    ]),
  },
  components: {
    CommonHeader: CommonHeader,
    Set: Set
  }

}

</script>



